/* ==============================
   GLOBAL RESET & BASE
================================ */
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Inter, system-ui, -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  background-color: #f1f3f6;
  color: #212121;
}

/* ==============================
   LOGIN PAGE
================================ */
.login-page {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-box {
  width: 340px;
  background: #ffffff;
  padding: 32px;
  text-align: center;
  border-radius: 12px;
  box-shadow: 0 8px 24px rgba(40, 116, 240, 0.15);
}

.login-box h2 {
  color: #2874f0;
  margin-bottom: 24px;
}

.login-box input {
  width: 100%;
  padding: 10px 12px;
  margin-bottom: 14px;
  border-radius: 6px;
  border: 1px solid #d0d7e2;
}

.login-box button {
  width: 100%;
  padding: 10px;
  background: #2874f0;
  color: #ffffff;
  border: none;
  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
}

/* ==============================
   MAIN LAYOUT
================================ */
.layout {
  display: flex;
  min-height: 100vh;
}

/* ==============================
   SIDEBAR (FIXED)
================================ */
/* ==============================
   SIDEBAR MENU (FINAL)
================================ */
.sidebar a {
  display: flex;
  align-items: center;

  color: #ffffff;
  padding: 14px 18px;
  margin-bottom: 8px;

  font-size: 16px;        /* ✅ bigger font */
  font-weight: 600;

  border-radius: 10px;
  text-decoration: none; /* ✅ removes underline */

  transition: background 0.2s ease;
}

/* VERY LIGHT HOVER */
.sidebar a:hover {
  background: rgba(255, 255, 255, 0.14);
}

/* OPTIONAL ACTIVE LOOK */
.sidebar a.active {
  background: rgba(255, 255, 255, 0.22);
}


/* ==============================
   CONTENT AREA
================================ */
.content {
  flex: 1;
  padding: 28px;
}

/* ==============================
   TOP NAVBAR
================================ */
.navbar {
  background: #ffffff;
  padding: 14px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  border-radius: 12px;
  box-shadow: 0 4px 12px rgba(40, 116, 240, 0.08);
}

.logout-btn {
  background: #2874f0;
  color: #ffffff;
  border: none;
  padding: 8px 16px;
  cursor: pointer;
  border-radius: 6px;
  font-weight: 600;
}

/* ==============================
   HEADINGS
================================ */
h1 {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 20px;
}

h2 {
  font-size: 22px;
  font-weight: 600;
}

/* ==============================
   FORMS & INPUTS
================================ */
input,
select {
  padding: 9px 12px;
  border: 1px solid #d0d7e2;
  border-radius: 6px;
  font-size: 14px;
}

input:focus,
select:focus {
  outline: none;
  border-color: #2874f0;
}

/* ==============================
   BUTTONS
================================ */
button {
  padding: 7px 16px;
  background: #2874f0;
  color: #ffffff;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 13px;
  font-weight: 600;
}

button.secondary {
  background: #e0e0e0;
  color: #333;
}

button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* ==============================
   DASHBOARD STATS
================================ */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 22px;
}

.stat-box {
  background: #ffffff;
  padding: 22px;
  border-radius: 14px;
  box-shadow: 0 6px 18px rgba(40, 116, 240, 0.12);
}

.stat-box p {
  color: #5f6368;
  margin: 0;
  font-size: 13px;
}

.stat-box h2 {
  margin: 10px 0;
  color: #000000;
  font-size: 30px;
  font-weight: 700;
}

/* ==============================
   TABLES
================================ */
table,
.data-table {
  width: 100%;
  border-collapse: collapse;
  background: #ffffff;
  border-radius: 14px;
  overflow: hidden;
  box-shadow: 0 6px 18px rgba(40, 116, 240, 0.12);
}

table th,
table td,
.data-table th,
.data-table td {
  padding: 14px;
  border-bottom: 1px solid #edf0f5;
  font-size: 14px;
}

table th,
.data-table th {
  background: #f5f8ff;
  font-weight: 600;
  color: #2874f0;
}

table tr:hover {
  background: #f0f6ff;
}

/* ==============================
   STATUS BADGES
================================ */
.status,
.order-status {
  padding: 4px 12px;
  border-radius: 999px;
  font-size: 12px;
  font-weight: 600;
}

.order-confirmed {
  background: #e3f2fd;
  color: #1565c0;
}

.order-shipped {
  background: #e8f0fe;
  color: #1a73e8;
}

.order-delivered {
  background: #e6f4ea;
  color: #137333;
}

/* ==============================
   MODAL
================================ */
.modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(40, 116, 240, 0.35);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal {
  background: #ffffff;
  padding: 26px;
  width: 380px;
  border-radius: 14px;
  box-shadow: 0 10px 30px rgba(40, 116, 240, 0.25);
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
}
/* =====================================
   SIDEBAR LINKS — FINAL WORKING FIX
===================================== */

.sidebar nav a {
  display: block;

  padding: 14px 18px;
  margin-bottom: 8px;

  font-size: 16px;          /* bigger font */
  font-weight: 600;

  color: #ffffff;
  text-decoration: none !important;  /* REMOVE UNDERLINE */

  border-radius: 10px;
  transition: background 0.2s ease;
}

/* hover: very light white box */
.sidebar nav a:hover {
  background: rgba(255, 255, 255, 0.14);
}

/* optional: active page look */
.sidebar nav a[aria-current="page"] {
  background: rgba(255, 255, 255, 0.22);
  font-weight: 700;
}
/* =====================================
   SIDEBAR — FORCE OVERRIDE (FINAL)
===================================== */

.sidebar nav a,
.sidebar a,
.sidebar nav a:visited,
.sidebar nav a:active {
  text-decoration: none !important;   /* REMOVE UNDERLINE */
  color: #ffffff !important;

  font-size: 16px !important;          /* INCREASE FONT */
  font-weight: 600 !important;

  display: block !important;
  padding: 14px 18px !important;
  margin-bottom: 8px !important;

  border-radius: 10px !important;
  background: transparent !important;

  transition: background 0.2s ease !important;
}

/* VERY LIGHT HOVER */
.sidebar nav a:hover,
.sidebar a:hover {
  background: rgba(255, 255, 255, 0.14) !important;
}

/* ACTIVE PAGE (ROUTER) */
.sidebar nav a[aria-current="page"] {
  background: rgba(255, 255, 255, 0.22) !important;
  font-weight: 700 !important;
}
